*{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;

}

body{
  padding: 60px;
  width: 100vw;
}

.gif-title{
  display: flex;
  justify-content: center;
  margin-right:300px;
  border: 1px solid rgba(24, 89, 24, 0.831);
  background-color: greenyellow;
  padding: 10px;
  width: 50%;
  text-align: center;
  box-shadow: 3px 5px 6px #ccc;
}

.head{
  font-family: 'Times New Roman', Times, serif;
  text-decoration: underline;
  text-shadow: 5px 2px 1px #ccc;
}

input{
  background-color: #ccc;
  color: rgb(40, 171, 49);
  font-size: 25px;
  width: 60%;
  border: 1px solid greenyellow;
  outline:none !important;
}

h3{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}


.card-grid{
  display: flex;
  flex: 0 0 auto;
  width: 1000px;
  height: auto;
  flex-wrap: wrap;
  justify-content: center;
}
.card{
align-content: center;
border: 1px solid rgba(72, 216, 72, 0.906);
border-radius: 6px;
overflow: hidden;
margin-bottom: 10px;
margin-left: 5px;
padding: 10px;
width: 300px;
height: 300px;
}
.card > img{
  width: 300px;
  max-height: 200px;
  align-items: center;
}

.card > p{
  padding: 5px;
  text-align: center;
}



.grid-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 10px;
}
.title-category{
  flex: 5 0 100%;
  color: rgba(77, 174, 77, 0.879);
  text-decoration: underline;
  justify-content: center;
}